import React from "react";

const CommentList = ({ comments }) => (
    <>
    <h3>Comments: </h3>
      {
        comments.map( (comment, index) => (
            <div className="comment" key={index}>
                <h4>{comment.username}</h4>
                <h4>{comment.text}</h4>
            </div>
        ))
    }
    </>
  
)
export default CommentList;
