/* eslint-disable react/no-array-index-key */
/* eslint-disable react/prop-types */
import React, { useState, useEffect } from 'react';
import ArticleList from '../components/ArticleList';
import articleContent from './article-content';
import NotFoundPage from './NotFound';
import CommentList from '../components/CommentsList';
import UpvoteText from '../components/UpvotesSection';
import AddCommentForm from '../components/AddCommentForm';

const ArticlePage = ({ match }) => {
  const { name } = match.params;

  const article = articleContent.find((item) => item.name === name);

  const [articleInfo, setArticleInfo] = useState({upvotes: 0, comments: []});

  useEffect(() => {
    const fetchData = async () => {
      const result = await fetch(`/api/articles/${name}`);
      const body = await result.json();
      setArticleInfo(body)
    }
    fetchData()
  },[name])
  if (!article) return <NotFoundPage />;

  const otherArticles = articleContent.filter((item) => item.name !== name);

  return (
    <>
      <h1>{article.title}</h1>
      <UpvoteText {...{ setArticleInfo }} upvotes={articleInfo.upvotes} articleName={name} />
      {article.content.map((paragraph, index) => (
        <p key={`${article.name}-${index}`}>{paragraph}</p>
      ))}
      <ArticleList articles={otherArticles} />
      <AddCommentForm {...{ setArticleInfo }}  articleName={name} />
      <CommentList comments={articleInfo.comments} />
    </>
  );
};

export default ArticlePage;
