import React from "react";

const UpvoteText = ({ upvotes, articleName, setArticleInfo }) => {
    
    const handleOnClickUpvoteArticle = async () => {
        try {
            const result = await fetch(`/api/articles/${articleName}/upvote`, {
                method: 'post',
            });
            const body = await result.json();
            setArticleInfo(body)
        } catch (error) {
            
        }
    }
    return (
    <div id="upvotes-section">
        <button onClick={handleOnClickUpvoteArticle} >Add upvote</button>
        <p>This post has been upvoted {upvotes} times</p> 
    </div >
)}
 export default UpvoteText; 